import './itemsContent.css'
import Spinner from "components/spinner/spinner";
import { useItems } from "hooks/useItems";
import usePaginate from "hooks/usePaginate";
import Cosmetic from "../cosmetic/cosmetic";
import ReactPaginate from 'react-paginate';


const ItemsContent = ({type}) => {

    const {cosmetics, isLoading} = useItems(type)

    const {currentItems, pageCount, handlePageClick} = usePaginate(cosmetics);

    return <div className={`tab-pane fade show active`} id={`pills-${type}`} role="tabpanel" aria-labelledby={`pills-${type}-tab`}>
        <div className="cosmetic-content">

            {
                isLoading
                ? <Spinner />
                : <Cosmetic type={type} cosmetics={currentItems}/>
            }
        </div>

        <ReactPaginate
          nextLabel=">"
          onPageChange={handlePageClick}
          pageRangeDisplayed={3}
          marginPagesDisplayed={2}
          pageCount={pageCount}
          previousLabel="<"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          breakLabel="..."
          breakClassName="page-item"
          breakLinkClassName="page-link"
          containerClassName="pagination"
          activeClassName="active"
          renderOnZeroPageCount={null} />
    </div>

}

export default ItemsContent;
